import { terminalColors as TC } from "@/utils/colors";
import { Time } from "@/utils/time";



export class Logger {
    private static log(type: "DEBUG" | "WARN" | "ERROR" | "INFO" | "SUCCESS", message: string, funcName?: string) {
      const colorMap = {
        DEBUG: TC.M,
        WARN: TC.BY,
        ERROR: TC.R,
        INFO: TC.B,
        SUCCESS: TC.G,
      };
      const color = colorMap[type] || TC.reset;
      const currentTime = Time.getTimeToLogFormat();
      const functionName = funcName ? `funcName: ${funcName}` : "";
      const logMethod = type === "ERROR" || type === "WARN" ? console.log : type === "INFO" ? console.info : console.log;
      logMethod(`${TC.dim}[${currentTime}]${TC.reset} ${color}[${type}]${TC.reset} ${functionName}: ${message}`);
    }
  
    static success(message: string, funcName?: string) {
      this.log("SUCCESS", message, funcName);
    }
  
    static error(message: string, funcName?: string) {
      this.log("ERROR", message, funcName);
    }
  
    static warn(message: string, funcName?: string) {
      this.log("WARN", message, funcName);
    }
  
    static info(message: string, funcName?: string) {
      this.log("INFO", message, funcName);
    }
  
    static debug(message: string, funcName?: string) {
      this.log("DEBUG", message, funcName);
    }
    static custom(type: string, message: string, funcName?: string) {
      const functionName = funcName ? `funcName: ${funcName}` : ""
      const currentTime = Time.getTimeToLogFormat()
      console.log(`${TC.dim}[${currentTime}]${TC.reset} ${TC.C}[${type}]${TC.reset} ${functionName}: ${message}`);
    }
  }
  