import { Logger } from "@/utils/logger";
import { Time } from "@/utils/time";
import { tool } from "ai";
import { z } from "zod";

export const TaskHandler = {
    /**
     * Get weather information for a specified city.
     * This tool requires the city name to be provided.
     */
    getWeather: tool({
        description: "Get weather information for a specified city.",
        parameters: z.object({
            city: z.string().min(1, "City is required").describe("Name of the city to get weather information for"),
        }),
        execute: async ({ city }) => await Tools.getWeather({ city }),
    }),
    /**
     * Get the current date and time in real-time.
     * This tool does not require any parameters. It returns the current time in a standard format.
     */
    getCurrentTime: tool({
        description: "Get the current date and time in real-time. This tool does not require any parameters. It returns the current time in a standard format. Use this tool when user ask you for current time or date or anything that related to time.",
        parameters: z.object({}),
        execute: async () => await Tools.getCurrentTime(),
    }),
}


export class Tools {

    /**
     * Retrieves the current time in a human-readable format.
     * This tool does not require any parameters.
     */
    public static async getCurrentTime() {
        Logger.info("Retrieving current time...");
        return Time.getCurrentTimeToHumanReadable()
    }


    /**
     * Retrieves weather information for a specified city.
     * This tool requires the city name to be provided.
     */
    public static async getWeather({ city }: { city: string }) {
        if (!city) {
            throw new Error("City must be provided.");
        }

        Logger.info(`Retrieving weather for city: ${city}`);
        // Simulate a weather response
        const weather = {
            city,
            temperature: "25Â°C",
            condition: "Sunny"
        };

        return weather;

    }
}