import * as rl from "node:readline/promises";
import dotenv from "dotenv";
dotenv.config();

const terminal = rl.createInterface({
  input: process.stdin,
  output: process.stdout,
  terminal: true,
  completer: (line) => {
    const completions = ["help", "exit", "clear"];
    const hits = completions.filter((c) => c.startsWith(line));
    return [hits.length ? hits : completions, line];
  },
});

export async function Question(question?: string): Promise<string> {
  return terminal.question(question ? question + ": " : ": ");
}

export async function CloseTerminal() {
  await terminal.close();
  process.exit(0);
}

export async function ClearTerminal() {
  process.stdout.write("\x1Bc");
  return terminal.prompt();
}

export async function Help() {
  console.log(`
Available commands:
  - help: Show this help message
  - exit: Exit the terminal
  - clear: Clear the terminal screen
  `);
  return terminal.prompt();
}


/**
 * Parses environment variables that start with a given prefix.
 * @param prefix The prefix to filter environment variables.
 * @returns An object containing arrays of keys and values.
 */
export function ParseEnvKeys(prefix: string): { keys: string[]; values: string[] } {
  const envKeys = Object.keys(process.env).filter((key) =>
    key.startsWith(prefix)
  );
  const keys: string[] = [];
  const values: string[] = [];
  
  envKeys.forEach((key) => {
    if (process.env[key]) {
      keys.push(key);
      values.push(process.env[key]!);
    }
  });

  return { keys, values };
}
