
export class Time {
  private static formatDateToParts(
    date: Date,
    timeZone: string
  ): { [key: string]: string } {
    const formatter = new Intl.DateTimeFormat("id-ID", {
      timeZone,
      year: "numeric",
      month: "2-digit",
      day: "2-digit",
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
      hour12: false,
    });

    const formattedParts = formatter.formatToParts(date);
    const dateParts: { [key: string]: string } = {};

    formattedParts.forEach(({ type, value }) => {
      dateParts[type] = value;
    });

    return dateParts;
  }

  private static formatDateString(dateParts: { [key: string]: string }): string {
    return `${dateParts.year}-${dateParts.month}-${dateParts.day}T${dateParts.hour}:${dateParts.minute}:${dateParts.second}Z`;
  }

  private static logFormat(dateParts: { [key: string]: string }): string {
    return `${dateParts.day}/${dateParts.month}/${dateParts.year}:${dateParts.hour}:${dateParts.minute}:${dateParts.second}`;
  }
  
  public static formatDateToHumanReadable(
    date: Date,
    timeZone: string
  ): string {
    const dateParts = Time.formatDateToParts(date, timeZone);
    return `${dateParts.day}/${dateParts.month}/${dateParts.year} ${dateParts.hour}:${dateParts.minute}:${dateParts.second}`;
  }

  

  public static getCurrentTime(): Date {
    const now = new Date();

    const dateParts = Time.formatDateToParts(now, "Asia/Jakarta");
    const formattedDateString = Time.formatDateString(dateParts);
    return new Date(formattedDateString);
  }

  public static getCurrentTimeToString(): string {
    const now = new Date();
    const dateParts = Time.formatDateToParts(now, "Asia/Jakarta");
    return Time.formatDateString(dateParts);
  }

  public static getCurrentTimeToHumanReadable(): string {
    const now = new Date();
    return Time.formatDateToHumanReadable(now, "Asia/Jakarta");
  }

  public static getTimeToLogFormat(): string {
    const now = new Date();
    const dateParts = Time.formatDateToParts(now, "Asia/Jakarta");
    return Time.logFormat(dateParts);
  }

}
