import * as crypto from "crypto";
import * as path from "path";
import * as fs from "fs";

import { Logger } from "@/utils/logger";
import { mimeType } from "@/utils/mimeType";
import { FileDownloadInterface, FileInterface, FileStorageInterface } from "@/types";

/** IOF (Input/Output File) class for handling file operations
 * - Calculates a SHA-256 hash of a given buffer.
 * - Provides a method to get the file path based on the hash.
 * - Allows setting a file location by saving the file and storing its metadata.
 * - Downloads a file from a URL and saves it to a specified path.
 * - Calculates the size of a file based on its buffer.
 * - Retrieves file paths based on hashes or buffers.
 * - Handles file storage operations using a repository pattern.
 * - Uses the FileStorageRepo for database interactions related to file storage.
 * - Uses the UserRepo for user-related database interactions.
 **/
export class IOF {

  /**
   * Creates a directory if it does not exist.
   * @param dirPath - The path of the directory to create.
   */
  public static mkdir(dirPath: string): void {
    try {
      if (!fs.existsSync(dirPath)) {
        Logger.info(`Creating directory: ${dirPath}`);
        fs.mkdirSync(dirPath, { recursive: true });
      }
    } catch (error) {
      throw new Error(`Failed to create directory at ${dirPath}:  ${error instanceof Error ? error.message : String(error)}`);
    }
  }

  /**
   * Calculates the SHA-256 hash of a given buffer.
   * @param buffer - The buffer to hash.
   * @returns The SHA-256 hash as a hexadecimal string.
   */
  public static calculateHashByBuffer(buffer: Buffer): string {
    if (!Buffer.isBuffer(buffer)) {
      throw new Error("Input must be a Buffer");
    }
    if (buffer.length === 0) {
      throw new Error("Buffer cannot be empty");
    }
    return crypto.createHash("sha256").update(buffer).digest("hex");
  }

  /**
   * Calculates the size of a file based on its buffer.
   * @param buffer - The buffer representing the file.
   * @returns The size of the file in bytes.
   */
  public static calculateSizeByBuffer(buffer: Buffer): number {
    if (!Buffer.isBuffer(buffer)) {
      throw new Error("Input must be a Buffer");
    }
    if (buffer.length === 0) {
      throw new Error("Buffer cannot be empty");
    }
    return Buffer.byteLength(buffer);
  }

  /**
   * Sets the file location by saving the file to the specified path and storing its metadata.
   * @param data - The file data including the file buffer, name, and path.
   * @returns The full path of the saved file or null if an error occurs.
   */
  public async setFileLocation(data: FileInterface): Promise<FileStorageInterface | null> {
    const hash = IOF.calculateHashByBuffer(Buffer.from(data.file));
    const size = IOF.calculateSizeByBuffer(Buffer.from(data.file));
    const type = mimeType(data.name);

    try {
      const fullPath = path.join(process.cwd(), data.path, data.name);
      const dir = path.dirname(fullPath);
      IOF.mkdir(dir);
      await fs.promises.writeFile(fullPath, data.file);

      return {
        filename: data.name,
        fileuri: fullPath,
        filehash: hash,
        filesize: size,
        filetype: type,
      };
    } catch (error) {
      throw new Error(`Failed to set file location: ${error instanceof Error ? error.message : String(error)}`);
    }
  }

  /**
   * Downloads a file from a given URL and saves it to the specified download path.
   * @param data - The file download data including the file URL and save path.
   * @returns The metadata of the downloaded file.
   */
  public static async downloadFile(data: FileDownloadInterface): Promise<FileStorageInterface> {
    try {
      const response = await fetch(data.fileuri);
      const buffer = await response.arrayBuffer();
      const fileName = path.basename(data.fileuri);
      const filePath = path.join(data.saveTo, fileName);
      fs.writeFileSync(filePath, Buffer.from(buffer));
      return {
        filename: fileName,
        fileuri: filePath,
        filehash: IOF.calculateHashByBuffer(Buffer.from(buffer)),
        filesize: IOF.calculateSizeByBuffer(Buffer.from(buffer)),
        filetype: mimeType(fileName),
      };
    } catch (error) {
      throw new Error(`Failed to download file: ${error}`);
    }
  }

  /**
   * Retrieves the string content of a text file.
   * @param filePath - The path to the text file.
   * @returns A promise that resolves to the content of the file as a string.
   * @throws An error if the file cannot be read.
   */
  public static async readTextFile(filePath: string): Promise<string> {
    try {
      const data = await fs.promises.readFile(filePath, "utf-8");
      return data;
    } catch (error) {
      throw new Error(`Failed to read file at ${filePath}`);
    }
  }

  /**
   * Converts a file to a generative path format.
   * @param fileName - The name of the file to convert.
   * @returns An object containing the inline data with base64 encoded content and MIME type.
   * @throws An error if the file cannot be read or converted.
   */
  public async fileToGenerativePath(fileName: string) {
    try {
      const mime = mimeType(fileName);
      const file = await fs.promises.readFile(fileName);
      return {
        inlineData: {
          data: Buffer.from(file).toString("base64"),
          mimeType: mime,
        },
      };
    } catch (error) {
      throw new Error(`Failed to convert file to generative path: ${error}`);
    }
  }
}
